import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { Lembur_pengaturan, Lembur, Pegawai } from 'src/app/sdk/hr/models';
import { KalenderLemburService, KalenderLemburPengaturanService, DirektoriPegawaiService } from 'src/app/sdk/hr/services';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private routerActive: ActivatedRoute,
        private kalenderLemburService: KalenderLemburService,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private kalenderLemburPengaturanService: KalenderLemburPengaturanService,
        private spinner: NgxSpinnerService,
        private router: Router
    ) { }



    lembur: Lembur = {}
    lembur_pengaturan: Lembur_pengaturan = {}

    id_lembur = ''

    ngOnInit() {
        this.id_lembur = this.routerActive.snapshot.paramMap.get('id_lembur')
        console.log(this.id_lembur)
        this.getData();

    }

    getData() {
        this.spinner.show()
        this.kalenderLemburService.getKalenderLembursId(this.id_lembur).subscribe(
            response => {
                this.lembur = response.data
                this.spinner.hide()
                this.getDetailPengaturan(response.data.id_lembur_pengaturan)
                this.getPegawai(response.data.id_pegawai)
            }
        )

    }

    getDetailPengaturan(id_lembur_pengaturan) {
        this.kalenderLemburPengaturanService.getKalenderLemburPengaturansId(id_lembur_pengaturan).subscribe(
            data => {
                this.lembur_pengaturan = data.data
            }
        )
    }

    pegawai: Pegawai = {}
    getPegawai(id_pegawai) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(id_pegawai).subscribe(
            data => {
                this.pegawai = data.data
            }
        )
    }


    goToBack() {
        this.router.navigate(['/hr/kalender/lembur'])
    }

    is_approved = false
    call_approved() {
        this.is_approved = !this.is_approved
    }

    approved() {
        this.spinner.show()
        this.kalenderLemburService.putKalenderLembursId({ id: this.id_lembur, body: this.lembur }).subscribe(
            data => {
                this.getData()
                this.spinner.hide()
                this.is_approved = !this.is_approved
            }
        )
    }


}
