import { Component, OnInit } from '@angular/core';
import { KalenderLemburService, KalenderLemburPengaturanService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Lembur_pengaturan, Lembur } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private kalenderLemburService: KalenderLemburService,
        private kalenderLemburPengaturanService: KalenderLemburPengaturanService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pegawaiDetailService: PegawaiDetailService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) { }

    id_pegawai: string = ''
    tanggal_lembur: string = ''
    listPengaturan: Lembur_pengaturan[] = []

    ngOnInit() {



        this.tanggal_lembur = this.activatedRoute.snapshot.paramMap.get('tanggal')
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log(data)
        })

        this.form()
        this.getPreeData()
    }

    getPreeData() {
        this.kalenderLemburPengaturanService.getKalenderLemburPengaturans().subscribe(
            data => {
                this.listPengaturan = data.data
            }
        )
    }

    dataForm: FormGroupTypeSafe<Lembur>
    form() {
        this.dataForm = this.fb.group<Lembur>({
            id_pegawai: new FormControl(this.id_pegawai, [Validators.required]),
            id_pegawai_posisi: new FormControl(''),
            id_lembur_pengaturan: new FormControl(''),
            lama_lembur: new FormControl(0, [Validators.required]),
            lama_disetujui: new FormControl(''),
            status_lembur: new FormControl(''),
            id_pegawai_setujui: new FormControl(''),
            tanggal_lembur: new FormControl(this.tanggal_lembur),
            keterangan_lembur: new FormControl(''),
            ref_id_absensi: new FormControl(''),
            id_user: new FormControl(''),

        })
    }

    simpan() {
        this.spinner.show()
        this.dataForm.patchValue({ id_pegawai: this.id_pegawai })
        this.dataForm.patchValue({ tanggal_lembur: this.tanggal_lembur })
        if (this.dataForm.invalid) {
            console.log(this.dataForm.value)
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }


        this.kalenderLemburService
            .postKalenderLemburs(this.dataForm.value)
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/employee/pegawai-detail', this.id_pegawai, 'lembur'])
    }



}
